<template>
    <div class="container-home" id="container-home" ref="container">
        <div class="cbox cweather" id='cbox-weather'>
            <Weather @changesize="changeSize"></Weather>
        </div>
        <div class="cbox cuser" id='cbox-user'>
            <User @motionstart="initMotion"></User>
        </div>
        <div class="cbox cvideo" id='cbox-video'>
            <Video></Video>
        </div>
        <div class="cbox cctime" id='cbox-time'>
            <Time></Time>
        </div>
        <div class="cbox cmap" id='cbox-map'>
            <Maps></Maps>
        </div>
    </div>
</template>
<script scoped>
    
    import Weather from 'components/weather/weather.vue';
    import User from 'components/user/user.vue';
    import Video from 'components/video/video.vue';
    import Time from 'components/time/time.vue';
    import Maps from 'components/map/map.vue';

    import toolDom from 'common/js/tool.dom.js'

    export default {
        data(){
            return {
                userOnoff:true,
                userAnimationOnoff: true
            }
        },
        methods:{
            //初始化交互事件
            initMotion(){

                if(!this.userAnimationOnoff){
                    return;
                }

                var timer = null;

                //获取当前的left
                let currentLeft = parseInt($('#cbox-user').css('left'));

                if(this.userOnoff){
                    $('#cbox-user').animate({'left': (currentLeft-290)},1000,'easeInOutQuint',()=>{
                        this.userAnimationOnoff = true;
                    });
                }else{
                    timer = setTimeout(()=>{
                        $('#cbox-user').animate({'left': (currentLeft+290)},1000,'easeInOutQuint',()=>{
                            this.userAnimationOnoff = true;
                        });
                        clearTimeout(timer);
                    },2200);
                }

                this.userOnoff = !this.userOnoff;
                this.userAnimationOnoff = false;
            },
            //初始化拖拽状态
            initDrags(){
                //1. 设置日历可拖拽
                toolDom.drag({
                    'target': $('#cbox-weather').get(0),
                    'parent': true
                });
                //2. 设置用户可拖拽
                toolDom.drag({
                    'target': $('#cbox-user').get(0),
                    'parent': true
                });
                //3. 设置视频可拖拽
                toolDom.drag({
                    'target': $('#cbox-video').get(0),
                    'parent': true
                });
                //4. 设置时间可拖拽
                toolDom.drag({
                    'target': $('#cbox-time').get(0),
                    'parent': true
                });
                //5. 设置地图可拖拽
                toolDom.drag({
                    'target': $('#cbox-map').get(0),
                    'parent': true
                });
            },
            transition(){
                this.$velocity(this.$refs.container,{
                    opacity: 1
                },1000)
            },
            changeSize(size){
                $('#cbox-weather').css({
                    width: size.w,
                    height: size.h
                });
            }
        },
        mounted(){
            this.transition();
            this.initDrags();
        },
        components:{
            Weather,
            User,
            Video,
            Time,
            Maps
        }
    }
</script>

<style scoped>
    .time{
        font-size: 14px;
        font-weight: bold;
    }
    .content{
        padding-left: 5px;
    }
</style>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus">
    @import "~common/stylus/mixin.styl"
    
    .container-home
        wh(100%,100%);opacity:0;position:relative;overflow:hidden;
        .cbox
            wh(auto,auto);position:absolute;ab(10,10);z-index:20;
        .cweather
            left:20px;top:20px;wh(500px,320px);
        .ctext
            wh(200px,200px);
        .cuser
            abs();right:-280px;top:100px;wh(280px,180px); 
        .cvideo
            ab(540px,20px);wh(550px,310px);
        .cctime
            ab(20px,400px);
        .cmap
            ab(420px,400px);            
</style>



