
class ToolDom{
    constructor(){
        this.dragTimer = null;
    }

    /**
     * [drag 拖拽函数封装]
     * @param  {[type]} opts [description]
     * @return {[type]}      [description]
     */
    drag(opts)   //拖拽元素 并且生成 克隆元素
    {   
        var This = this;
        var obj = opts.target;
        var defaults = {
            parent: true,
            dragStart:function(x,y){},
            dragEnd:function(x,y){},
            dragMove:function(x,y){}
        };
        
        var opts = Object.assign(defaults,opts);

        obj.onmousedown = function(ev)
        {   
            //清除定时器
            cancelAnimationFrame(This.dragTimer);
            document.onmouseup = null;

            var ev = ev || window.event;
            var sx = ev.clientX;
            var sy = ev.clientY;
            var st = new Date();
            var sX = ev.clientX - this.getBoundingClientRect().left;
            var sY = ev.clientY - this.getBoundingClientRect().top;
            var ow = this.offsetWidth;
            var oh = this.offsetHeight;
            var parentWidth = document.body.clientWidth;
            var parentHeight = document.body.clientHeight;
            var disW = 0;
            var disH = 0;
            var onoff = false;
            
            //设置目标元素的托拖拽属性
            obj.style.position = 'absolute';

            if(opts.parent){
                obj.parentNode.style.position = 'relative';
                disW = obj.parentNode.clientWidth - ow;
                disH = obj.parentNode.clientHeight - oh;
                onoff = true;
            }

            //1.检测拖拽行为  2.获取鼠标抬起的位置
            var checkDisX = 0;
            var checkDisY = 0;

            var lastX = 0;
            var lastY = 0;

            //obj.style.left = 20 + 'px';
            var moveOnff = false;
            //console.log(obj.style.left,obj.style.top);

            document.onmousemove = function(ev)
            {   
                moveOnff = true;

                var ev = ev || window.event;

                var eX = ev.clientX - sX;
                var eY = ev.clientY - sY;

                //设定X拖动范围
                if(eX < 0)
                {
                    eX = 0;
                }
                else if(eX > disW)
                {
                    eX = disW;
                }

                //设定Y拖动范围
                if(eY < 0)
                {
                    eY = 0;
                }
                else if(eY > disH)
                {
                    eY = disH;
                }

                obj.style.left = eX + 'px';
                obj.style.top = eY + 'px';

                lastX = eX;
                lastY = eY;
            };

            document.onmouseup = function(e)
            {   

                if(!moveOnff){
                    document.onmousemove=document.onmouseup = null;
                    return false;
                }

                //在鼠标松开后做匀减速运动
                //1. 计算当前的位置坐标
                var dt = (new Date() - st)/5;
                var ex = lastX;
                var ey = lastY;
                var dx = e.clientX - sx;
                var dy = e.clientY - sy;
                var speedX = dx/dt;
                var speedY = dy/dt;
                var timer = null;
               
                function fn(){

                    This.dragTimer = requestAnimationFrame(fn);

                    speedX *= 0.90;
                    speedY *= 0.90;

                    ex = ex + speedX;
                    ey = ey + speedY;

                    //边界检测
                    if(ex <= 0 || ex+ow >= parentWidth){
                        speedX = -speedX;
                    }
                    if(ey <= 0 || ey+oh >= parentHeight){
                        speedY = -speedY;
                    }

                    //停止检测
                    if(Math.abs(speedX) < 0.2 && Math.abs(speedY) < 0.2){
                        cancelAnimationFrame(This.dragTimer);
                        document.onmouseup = null;
                    }
                    
                    obj.style.left = ex + 'px';
                    obj.style.top = ey + 'px';
                }

                fn();

                document.onmousemove = null;
            };

            ev.cancelBubble = true;
            ev.stopPropagation();
            return false;
        };
        
    }
}

export default new ToolDom();


